.stars {
  $currentNameOfClass: &;

  display: flex;

  &__star {
    height: 16px;
    width: 16px;

    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;

    &:not(:last-child) {
      margin-right: 4px;
    }

    @for $starsIndex from 1 through 5 {
      #{$currentNameOfClass}--#{$starsIndex} &:nth-child(-n + #{$starsIndex}) {
        background-image: url(../images/star-active.svg);
      }
    }
  }
}


